@mixin phone {
    @media (max-width: #{$phone}) {
      @content;
    }
}
  
  @mixin tablet{
    @media (min-width: #{$tablet}) and (max-width: #{$desktop - 1px}) {
      @content;
    }
  }
  
  @mixin desktop {
    @media (min-width: #{$desktop}) {
      @content;
    }
  }
  
 